<template>
  <div class="p-10">
    <div class="max-w-full mx-auto rounded">
      <div class="relative flex flex-col w-full h-full bg-white shadow-md text-slate-700 rounded-xl bg-clip-border">
        <!-- Search Bar -->
        <div class="p-4">
          <select v-model="searchField" class="w-full p-2 mb-2 border rounded border-slate-300">
            <option value="name">Recherche par Nom</option>
            <option value="pays">Recherche par Pays</option>
          </select>
          <input
            v-model="searchQuery"
            type="text"
            placeholder="Search..."
            class="w-full p-2 border rounded border-slate-300"
          />
        </div>

        <!-- Table Header and Actions -->
        <div class="relative mx-4 mt-4 overflow-hidden bg-white text-slate-700">
          <div class="flex items-center justify-between">
            <h3 class="text-lg font-semibold capitalize text-slate-800">Liste des Agences</h3>
            <button
              @click="showAddModal = true"
              class="flex items-center gap-2 rounded bg-slate-800 py-2.5 px-4 text-xs font-semibold text-white"
            >
              Ajouter Agence
            </button>
          </div>
        </div>

        <!-- Agencies Table -->
        <div class="p-0 overflow-y-scroll">
          <table class="w-full mt-4 text-left table-auto min-w-max">
            <thead>
              <tr>
                <th class="p-4 border-y border-slate-200 bg-slate-50">Nom</th>
                <th class="p-4 border-y border-slate-200 bg-slate-50">Pays</th>
                <th class="p-4 border-y border-slate-200 bg-slate-50">Modification</th>
                <th class="p-4 border-y border-slate-200 bg-slate-50">Suppression</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="agence in filteredAgences" :key="agence.id">
                <td class="p-4 font-bold capitalize border-b border-slate-200">{{ agence.name }}</td>
                <td class="p-4 font-semibold uppercase border-b border-slate-200">{{ agence.pays }}</td>
                <td class="p-4 border-b border-slate-200">
                  <button @click="editAgence(agence)" class="px-2 py-1 text-white bg-blue-500 rounded">Modifier</button>
                </td>
                  <td class="p-4 border-b border-slate-200">
                  <button @click="deleteAgence(agence.id)" class="px-2 py-1 text-white bg-red-500 rounded">Supprimer</button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <!-- Add/Edit Modal -->
    <div v-if="showAddModal || showEditModal" class="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50">
      <div class="p-4 bg-white rounded-xl shadow-md w-[30%]">
        <h3 class="mb-4 text-lg font-semibold">
          {{ showAddModal ? 'Ajouter Agence' : 'Modifier Agence' }}
        </h3>
        <form @submit.prevent="showAddModal ? addAgence() : updateAgence()">
          <div class="mb-4">
            <label class="block mb-2">Nom</label>
            <input v-model="currentAgence.name" type="text" class="w-full p-2 border rounded" required />
          </div>
          <div class="mb-4">
            <label class="block mb-2">Pays</label>
            <input v-model="currentAgence.pays" type="text" class="w-full p-2 border rounded" required />
          </div>
          <div class="flex justify-center gap-4">
            <button type="button" @click="closeModal" class="px-4 py-2 text-gray-700 bg-gray-200 rounded hover:bg-gray-700 hover:text-white">Annuler</button>
            <button type="submit" class="px-4 py-2 text-white bg-blue-500 rounded hover:bg-blue-700">
              {{ showAddModal ? 'Ajouter' : 'Modifier' }}
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
data() {
  return {
    agences: [], // Tableau vide
    searchQuery: "",
    searchField: "name",
    showAddModal: false,
    showEditModal: false,
    currentAgence: { id: null, name: "", pays: "" },
  };
},

 computed: {
  filteredAgences() {
    if (!Array.isArray(this.agences)) {
      return []; // Renvoie un tableau vide si `this.agences` n'est pas un tableau
    }
    return this.agences.filter((agence) =>
      agence[this.searchField]?.toLowerCase().includes(this.searchQuery.toLowerCase())
    );
  },
},

  mounted() {
    this.fetchAgences();
  },
  methods: {
 async fetchAgences() {
  try {
    const response = await axios.get("http://127.0.0.1:8000/api/agences", {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
    });
    console.log("API Response:", response.data); // Vérifiez la structure des données
    this.agences = response.data.agences; // Assurez-vous d'accéder à la propriété correcte
  } catch (error) {
    console.error("Error fetching agences:", error);
  }
},
    addAgence() {
      axios
        .post("http://127.0.0.1:8000/api/agences", this.currentAgence, {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        })
        .then(() => {
          this.fetchAgences();
          this.closeModal();
        })
        .catch((error) => console.error(error));
    },
    editAgence(agence) {
      this.currentAgence = { ...agence };
      this.showEditModal = true;
    },
    updateAgence() {
      axios
        .put(`http://127.0.0.1:8000/api/agences/${this.currentAgence.id}`, this.currentAgence , {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        })
        .then(() => {
          this.fetchAgences();
          this.closeModal();
        })
        .catch((error) => console.error(error));
    },
    deleteAgence(id) {
      if (confirm("Êtes-vous sûr de vouloir supprimer cette agence ?")) {
        axios
          .delete(`http://127.0.0.1:8000/api/agences/${id}`, {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        })
          .then(() => this.fetchAgences())
          .catch((error) => console.error(error));
      }
    },
/*************  ✨ Codeium Command ⭐  *************/
    /**
     * Ferme le modal d'ajout ou de modification d'une agence.
     * Réinitialise les champs du formulaire.
     */

/******  a892b2a6-2ee6-4bd2-9e0d-310cda64383e  *******/
    closeModal() {
      this.showAddModal = false;
      this.showEditModal = false;
      this.currentAgence = { id: null, name: "", pays: "" };
    },
  },
};
</script>

