<template>
  <div class="relative mx-4 mt-4 overflow-hidden bg-white rounded-none text-slate-700 bg-clip-border">
    <div class="flex items-center justify-between">
      <div>
        <h3 class="text-lg font-semibold capitalize text-slate-800">Gestion des Jours Fériés</h3>
      </div>
      <div class="flex flex-col gap-2 shrink-0 sm:flex-row">
        <button
          @click="openModal"
          class="flex select-none items-center gap-2 rounded bg-slate-800 py-2.5 px-4 text-xs font-semibold text-white shadow-md shadow-slate-900/10 transition-all hover:shadow-lg hover:shadow-slate-900/20 focus:opacity-[0.85] focus:shadow-none active:opacity-[0.85] active:shadow-none disabled:pointer-events-none disabled:opacity-50 disabled:shadow-none"
          type="button"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 24 24"
            fill="currentColor"
            aria-hidden="true"
            stroke-width="2"
            class="w-4 h-4"
          >
            <path
              d="M6.25 6.375a4.125 4.125 0 118.25 0 4.125 4.125 0 01-8.25 0zM3.25 19.125a7.125 7.125 0 0114.25 0v.003l-.001.119a.75.75 0 01-.363.63 13.067 13.067 0 01-6.761 1.873c-2.472 0-4.786-.684-6.76-1.873a.75.75 0 01-.364-.63l-.001-.122zM19.75 7.5a.75.75 0 00-1.5 0v2.25H16a.75.75 0 000 1.5h2.25v2.25a.75.75 0 001.5 0v-2.25H22a.75.75 0 000-1.5h-2.25V7.5z"
            ></path>
          </svg>
          Ajouter un Jour Férié
        </button>
      </div>
    </div>

    <!-- Users Table -->
    <div class="p-0 overflow-y-scroll">
      <table class="w-full min-w-full mt-4 text-left table-auto">
        <thead>
          <tr>
            <th
              class="p-1 transition-colors cursor-pointer border-y border-slate-200 bg-slate-50 hover:bg-slate-100"
            >
              <p
                class="flex items-center justify-between gap-2 font-sans text-sm font-normal leading-none text-slate-500"
              >
                Date
              </p>
            </th>
            <th
              class="p-4 transition-colors cursor-pointer border-y border-slate-200 bg-slate-50 hover:bg-slate-100"
            >
              <p
                class="flex items-center justify-between gap-2 font-sans text-sm font-normal leading-none text-slate-500"
              >
                Nom
              </p>
            </th>
            <th
              class="p-4 transition-colors cursor-pointer border-y border-slate-200 bg-slate-50 hover:bg-slate-100"
            >
              <p
                class="flex items-center justify-between gap-2 font-sans text-sm font-normal leading-none text-slate-500"
              >
                Actions
              </p>
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="jourFerie in joursFeries" :key="jourFerie.id">
            <td class="p-4 border-b border-slate-200">
              <div class="flex items-center gap-3">
                <div class="flex flex-col">
                  <p class="text-sm font-semibold text-slate-700">
                    {{ jourFerie.date_ferie }}
                  </p>
                </div>
              </div>
            </td>
            <td class="p-4 border-b border-slate-200">
              <div class="flex items-center gap-3">
                <div class="flex flex-col">
                  <p class="text-sm font-semibold text-slate-700">
                    {{ jourFerie.nom_ferie }}
                  </p>
                </div>
              </div>
            </td>
            <td class="p-4 border-b border-slate-200">
              <div class="flex items-center gap-2">
                <button
                  @click="editJourFerie(jourFerie)"
                  class="px-2 py-1 text-white rounded bg-stone-800"
                >
                  Modifier
                </button>
                <button
                  @click="deleteJourFerie(jourFerie.id)"
                  class="px-2 py-1 text-white bg-red-900 rounded"
                >
                  Supprimer
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Modal pour ajouter/modifier un jour férié -->
    <div
      v-if="isModalOpen"
      class="fixed inset-0 z-50 flex items-center justify-center p-6 bg-black bg-opacity-50 backdrop-blur-sm"
    >
      <div class="w-full max-w-md p-6 bg-white rounded-lg shadow-lg">
        <h3 class="mb-4 text-lg font-semibold text-gray-800">{{ modalTitle }}</h3>

        <div class="mb-4">
          <label for="date_ferie" class="block text-sm font-medium text-gray-700"
            >Date</label
          >
          <input
            id="date_ferie"
            v-model="form.date_ferie"
            type="date"
            class="w-full p-2 border border-gray-300 rounded-md focus:ring focus:ring-blue-300"
          />
        </div>

        <div class="mb-4">
          <label for="nom_ferie" class="block text-sm font-medium text-gray-700"
            >Nom</label
          >
          <input
            id="nom_ferie"
            v-model="form.nom_ferie"
            type="text"
            class="w-full p-2 border border-gray-300 rounded-md focus:ring focus:ring-blue-300"
          />
        </div>

        <div class="flex justify-center space-x-4">
          <button
            @click="saveJourFerie"
            class="px-4 py-2 text-white bg-blue-500 rounded hover:bg-blue-600"
          >
            Enregistrer
          </button>
          <button
            @click="closeModal"
            class="px-4 py-2 text-white bg-gray-500 rounded hover:bg-gray-600"
          >
            Annuler
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      joursFeries: [],
      isModalOpen: false,
      modalTitle: "",
      form: {
        id: null,
        date_ferie: "",
        nom_ferie: "",
      },
    };
  },
  mounted() {
    this.fetchJoursFeries();
  },
  methods: {
    async fetchJoursFeries() {
      try {
        const response = await axios.get("http://127.0.0.1:8000/api/jours-feries", {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
        this.joursFeries = response.data; // Stocke les jours fériés
      } catch (error) {
        console.error("Erreur lors de la récupération des jours fériés:", error);
      }
    },
    openModal() {
      this.modalTitle = "Ajouter un Jour Férié";
      this.form = {
        id: null,
        date_ferie: "",
        nom_ferie: "",
      };
      this.isModalOpen = true;
    },
    editJourFerie(jourFerie) {
      this.modalTitle = "Modifier un Jour Férié";
      this.form = { ...jourFerie };
      this.isModalOpen = true;
    },
    saveJourFerie() {
      if (this.form.id) {
        axios
          .put(`http://127.0.0.1:8000/api/jours-feries/${this.form.id}`, this.form, {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          })
          .then(() => {
            this.fetchJoursFeries();
            this.closeModal();
          })
          .catch((error) => {
            console.error("Erreur lors de la mise à jour du jour férié:", error);
          });
      } else {
        axios
          .post("http://127.0.0.1:8000/api/jours-feries", this.form, {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          })
          .then(() => {
            this.fetchJoursFeries();
            this.closeModal();
          })
          .catch((error) => {
            console.error("Erreur lors de l'ajout du jour férié:", error);
          });
      }
    },
    deleteJourFerie(id) {
      axios
        .delete(`http://127.0.0.1:8000/api/jours-feries/${id}`, {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        })
        .then(() => {
          this.fetchJoursFeries();
        })
        .catch((error) => {
          console.error("Erreur lors de la suppression du jour férié:", error);
        });
    },
    closeModal() {
      this.isModalOpen = false;
      this.form = {
        id: null,
        date_ferie: "",
        nom_ferie: "",
      };
    },
  },
};
</script>

