<template>
  <div class="max-w-4xl p-6 mx-auto bg-white rounded-lg shadow-md">
    <div class="flex justify-between items-center mb-6">
      <h1 class="text-2xl font-bold">Gestion des Niveaux de Compétence</h1>
      <router-link
        to="/"
        class="px-4 py-2 text-white bg-blue-600 rounded-md hover:bg-blue-700"
      >
        Retour
      </router-link>
    </div>

    <!-- Formulaire d'ajout / modification d'un niveau de compétence -->
    <form @submit.prevent="saveNiveau" class="space-y-4">
      <div class="flex space-x-4">
        <div class="flex-1">
          <label for="type" class="block text-sm font-medium text-gray-700"
            >Type de Niveau</label
          >
          <input
            v-model="niveauForm.type"
            required
            class="block w-full h-10 mt-1 border border-gray-300 rounded-md focus:ring focus:ring-indigo-200"
            placeholder="Entrez le type de niveau"
          />
        </div>
        <div class="flex-1">
          <label for="niveau" class="block text-sm font-medium text-gray-700"
            >Niveau de Compétence</label
          >
          <input
            v-model="niveauForm.niveau"
            required
            class="block w-full h-10 mt-1 border border-gray-300 rounded-md focus:ring focus:ring-indigo-200"
            placeholder="Entrez le niveau de compétence"
          />
        </div>
      </div>

      <button
        type="submit"
        class="w-full h-10 text-white transition duration-200 bg-indigo-600 rounded-md hover:bg-indigo-700"
      >
        {{ isEdit ? "Modifier" : "Ajouter" }} Niveau
      </button>
    </form>

    <!-- Liste des niveaux de compétence regroupés par type -->
    <div class="mt-6">
      <div v-for="type in uniqueTypes" :key="type" class="mb-4">
        <h2 class="text-xl font-semibold">{{ type }}</h2>
        <table class="min-w-full bg-white border border-gray-300">
          <thead>
            <tr>
              <th class="px-4 py-2 border-b">Niveau de Compétence</th>
              <th class="px-4 py-2 border-b">Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="niveau in niveaux.filter((n) => n.type === type)" :key="niveau.id">
              <td class="px-4 py-2 border-b">{{ niveau.niveau }}</td>
              <td class="px-4 py-2 border-b">
                <button
                  @click="editNiveau(niveau)"
                  class="text-blue-600 hover:text-blue-800"
                >
                  Modifier
                </button>
                <button
                  @click="deleteNiveau(niveau.id)"
                  class="ml-2 text-red-600 hover:text-red-800"
                >
                  Supprimer
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      niveaux: [],
      niveauForm: {
        niveau: "",
        type: "", // Ajout du type de niveau
      },
      isEdit: false,
      editNiveauId: null,
    };
  },
  computed: {
    uniqueTypes() {
      return [...new Set(this.niveaux.map((niveau) => niveau.type))];
    },
  },
  methods: {
    fetchNiveaux() {
      axios
        .get("http://127.0.0.1:8000/api/niveaux-competence", {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        })
        .then((response) => {
          this.niveaux = response.data;
        })
        .catch((error) => {
          console.error(
            "Il y a eu une erreur lors de la récupération des niveaux :",
            error
          );
        });
    },
    saveNiveau() {
      const request = this.isEdit
        ? axios.put(
            `http://127.0.0.1:8000/api/niveaux-competence/${this.editNiveauId}`,
            this.niveauForm,
            {
              headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${localStorage.getItem("token")}`,
              },
            }
          )
        : axios.post("http://127.0.0.1:8000/api/niveaux-competence", this.niveauForm, {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          });

      request
        .then(() => {
          this.fetchNiveaux();
          this.resetForm();
        })
        .catch((error) => {
          console.error(
            "Il y a eu une erreur lors de l'enregistrement du niveau :",
            error
          );
        });
    },
    editNiveau(niveau) {
      this.niveauForm = {
        niveau: niveau.niveau,
        type: niveau.type, // Ajout du type lors de la modification
      };
      this.isEdit = true;
      this.editNiveauId = niveau.id;
    },
    deleteNiveau(id) {
      axios
        .delete(`http://127.0.0.1:8000/api/niveaux-competence/${id}`, {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        })
        .then(() => {
          this.fetchNiveaux();
        })
        .catch((error) => {
          console.error("Il y a eu une erreur lors de la suppression du niveau :", error);
        });
    },
    resetForm() {
      this.niveauForm = { niveau: "", type: "" }; // Réinitialisation du type
      this.isEdit = false;
      this.editNiveauId = null;
    },
  },
  mounted() {
    this.fetchNiveaux();
  },
};
</script>

