<template>
  <div class="p-10">
    <div class="max-w-full mx-auto rounded">
      <div class="relative flex flex-col w-full h-full bg-white shadow-md text-slate-700 rounded-xl bg-clip-border">
        <!-- Search Bar -->
        <div class="p-4">
          <input
            v-model="searchQuery"
            type="text"
            placeholder="Rechercher par objet..."
            class="w-full p-2 border rounded border-slate-300"
          />
        </div>

        <!-- Table Header and Actions -->
        <div class="relative mx-4 mt-4 overflow-hidden bg-white rounded-none text-slate-700 bg-clip-border">
          <div class="flex items-center justify-between">
            <div>
              <h3 class="text-lg font-semibold capitalize text-slate-800">
                Liste des demandes
              </h3>
            </div>
            <div class="flex flex-col gap-2 shrink-0 sm:flex-row">
              <button
                @click="openAddModal"
                class="flex select-none items-center gap-2 rounded bg-slate-800 py-2.5 px-4 text-xs font-semibold text-white shadow-md shadow-slate-900/10 transition-all hover:shadow-lg hover:shadow-slate-900/20 focus:opacity-[0.85] focus:shadow-none active:opacity-[0.85] active:shadow-none disabled:pointer-events-none disabled:opacity-50 disabled:shadow-none"
                type="button"
              >
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" aria-hidden="true" stroke-width="2" class="w-4 h-4">
                  <path d="M6.25 6.375a4.125 4.125 0 118.25 0 4.125 4.125 0 01-8.25 0zM3.25 19.125a7.125 7.125 0 0114.25 0v.003l-.001.119a.75.75 0 01-.363.63 13.067 13.067 0 01-6.761 1.873c-2.472 0-4.786-.684-6.76-1.873a.75.75 0 01-.364-.63l-.001-.122zM19.75 7.5a.75.75 0 00-1.5 0v2.25H16a.75.75 0 000 1.5h2.25v2.25a.75.75 0 001.5 0v-2.25H22a.75.75 0 000-1.5h-2.25V7.5z"></path>
                </svg>
                Ajouter Demande
              </button>
            </div>
          </div>
        </div>

        <!-- Demandes Table -->
        <div class="p-0 overflow-y-scroll">
          <table class="w-full mt-4 text-left table-auto min-w-max text-[9px]">
            <thead>
              <tr>
                <th class="p-4 transition-colors cursor-pointer border-y border-slate-200 bg-slate-50 hover:bg-slate-100">Objet</th>
                <th class="p-4 transition-colors cursor-pointer border-y border-slate-200 bg-slate-50 hover:bg-slate-100">Utilisateur</th>
                <th class="p-4 transition-colors cursor-pointer border-y border-slate-200 bg-slate-50 hover:bg-slate-100">Voir +</th>
                <th class="p-4 transition-colors cursor-pointer border-y border-slate-200 bg-slate-50 hover:bg-slate-100">Supprimer</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="demande in filteredDemandes" :key="demande.id">
                <td class="p-4 border-b border-slate-200">
                  <p class="text-sm font-semibold text-slate-700">{{ demande.object }}</p>
                </td>
                <td class="p-4 border-b border-slate-200">
                  <p class="text-sm text-slate-500">{{ demande.user.name }}</p>
                </td>
                <td class="p-4 border-b border-slate-200">
                  <button @click="openConsultModal(demande)" class="mr-2 text-blue-500 hover:underline">Consulter</button>
                </td>
                <td class="p-4 border-b border-slate-200">
                  <button @click="deleteDemande(demande.id)" class="text-red-500 hover:underline">Supprimer</button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <!-- Modal: Add Demande -->
    <div v-if="showAddModal" class="fixed inset-0 z-50 flex items-center justify-center">
      <div class="absolute inset-0 bg-gray-900 opacity-50"></div>
      <div class="z-50 w-full max-w-md p-6 bg-white rounded-lg">
        <h2 class="mb-4 text-xl font-bold">Ajouter une demande</h2>
        <form @submit.prevent="submitForm">
          <div class="mb-4">
            <label for="object" class="block mb-2 font-bold text-gray-700">Objet</label>
            <input type="text" id="object" v-model="form.object" class="w-full px-3 py-2 border rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500" placeholder="Objet de la demande" required>
          </div>
          <div class="mb-4">
            <label for="commentaire" class="block mb-2 font-bold text-gray-700">Commentaire</label>
            <textarea id="commentaire" v-model="form.commentaire" class="w-full px-3 py-2 border rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500" placeholder="Votre commentaire" rows="4" required></textarea>
          </div>
          <div class="flex justify-end">
            <button type="button" @click="closeAddModal" class="px-4 py-2 mr-2 text-gray-700 bg-gray-300 rounded-full shadow-lg">Annuler</button>
            <button type="submit" class="px-4 py-2 text-white bg-blue-500 rounded-full shadow-lg">Soumettre</button>
          </div>
        </form>
      </div>
    </div>

    <!-- Modal: Consult Demande -->
    <div v-if="showConsultModal" class="fixed inset-0 z-50 flex items-center justify-center">
      <div class="absolute inset-0 bg-gray-900 opacity-50"></div>
      <div class="z-50 w-full max-w-md p-6 bg-white rounded-lg">
        <h2 class="mb-4 text-xl font-bold">Consulter la demande</h2>
        <div class="mb-4">
          <label class="block mb-2 font-bold text-gray-700">Objet</label>
          <p>{{ consultDemande.object }}</p>
        </div>
        <div class="mb-4">
          <label class="block mb-2 font-bold text-gray-700">Commentaire</label>
          <p>{{ consultDemande.commentaire }}</p>
        </div>
        <div class="mb-4">
          <label class="block mb-2 font-bold text-gray-700">Utilisateur</label>
          <p>{{ consultDemande.user.name }}</p>
        </div>
        <div class="flex justify-end">
          <button type="button" @click="closeConsultModal" class="px-4 py-2 text-gray-700 bg-gray-300 rounded-full shadow-lg">Fermer</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      demandes: [],
      searchQuery: "",
      showAddModal: false,
      showConsultModal: false,
      consultDemande: {},
      form: {
        object: '',
        commentaire: ''
      }
    };
  },
  computed: {
    filteredDemandes() {
      if (!Array.isArray(this.demandes)) {
        return [];
      }
      return this.demandes.filter(demande => {
        const matchesQuery = demande.object ? demande.object.toLowerCase().includes(this.searchQuery.toLowerCase()) : false;
        return matchesQuery;
      });
    }
  },
  mounted() {
    this.fetchDemandes();
  },
  methods: {
    async fetchDemandes() {
      try {
        const response = await axios.get('http://127.0.0.1:8000/api/demandes', {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        });
        this.demandes = response.data.demandes;
      } catch (error) {
        console.error('Error fetching demandes:', error);
      }
    },
    openAddModal() {
      this.showAddModal = true;
    },
    closeAddModal() {
      this.showAddModal = false;
    },
    openConsultModal(demande) {
      this.consultDemande = demande;
      this.showConsultModal = true;
    },
    closeConsultModal() {
      this.showConsultModal = false;
    },
    async submitForm() {
      try {
        const response = await axios.post('http://127.0.0.1:8000/api/demandes', {
          user_id: localStorage.getItem('userId'),
          object: this.form.object,
          commentaire: this.form.commentaire
        }, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        });
        this.demandes.push(response.data);
        this.closeAddModal();
        this.fetchDemandes();
        this.form.object = '';
        this.form.commentaire = '';
      } catch (error) {
        console.error('Error submitting request:', error);
      }
    },
    async deleteDemande(id) {
      try {
        await axios.delete(`http://127.0.0.1:8000/api/demandes/${id}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`
          }
        });
        this.demandes = this.demandes.filter(demande => demande.id !== id);
      } catch (error) {
        console.error('Error deleting request:', error);
      }
    }
  }
};
</script>

<style scoped>
/* Add any additional styles here */
</style>
