<template>
  <div class="flex items-center justify-center min-h-screen p-6 bg-gray-100">
    <div class="container relative max-w-screen-lg mx-auto">
        <!-- Panneau des compétences - Slide in smoothly -->
      <transition name="slide-fade">
        <div
          v-if="showCompetences"
          class="absolute left-0 w-64 h-full p-6 bg-white border-r rounded-r-lg shadow-lg top-10"
        >
          <h3 class="text-lg font-bold text-center">Compétences</h3>
          <ul class="gap-4 mt-4 space-y-2">
            <li
              v-for="competence in user.domaines_competence"
              :key="competence.user_domaine_competence_id"
              class="flex items-center justify-between p-2 bg-gray-100 rounded-lg"
            >
              <span class="text-xs">
                {{ competence.domaine_nom }} - Niveau: {{ competence.niveau_type }} {{ competence.niveau_competence }}
              </span>
              <button
                @click="deleteCompetence(competence.user_domaine_competence_id)"
                class="ml-2 text-xs font-semibold text-red-600 hover:text-red-800"
              >
                Supprimer
              </button>
            </li>
          </ul>
        </div>
      </transition>
      <!-- Section principale du formulaire -->
      <div class="p-4 px-4 mb-6 bg-white rounded-lg shadow-lg md:p-8">
        <div class="grid items-center justify-center grid-cols-1 gap-4 text-sm gap-y-2 lg:grid-cols-3">
          <div class="text-gray-600">
            <img src="@/assets/sbsN.jpeg" alt="" class="mb-2 rounded-lg" />
            <p class="text-lg font-medium">Modifier un Consultant</p>
            <p class="text-xs">
              S'il vous plaît, remplissez les champs que vous désirez modifier
            </p>
            <div v-if="errorMessage" class="text-red-500">{{ errorMessage }}</div>
          </div>

          <div class="lg:col-span-2">
            <div class="grid grid-cols-1 gap-4 text-sm gap-y-2 md:grid-cols-5">
              <div class="md:col-span-5">
                <label for="name">Noms</label>
                <input
                  type="text"
                  v-model="form.name"
                  id="name"
                  class="w-full h-10 px-4 mt-1 border rounded bg-gray-50 focus:ring focus:ring-blue-300"
                  placeholder="Noms"
                />
              </div>

              <div class="md:col-span-5">
                <label for="email">Email Address</label>
                <input
                  type="email"
                  v-model="form.email"
                  id="email"
                  class="w-full h-10 px-4 mt-1 border rounded bg-gray-50 focus:ring focus:ring-blue-300"
                  placeholder="email@domain.com"
                />
              </div>

              <div class="md:col-span-5">
                <label for="password">Mot de Passe</label>
                <input
                  type="password"
                  v-model="form.password"
                  id="password"
                  class="w-full h-10 px-4 mt-1 border rounded bg-gray-50 focus:ring focus:ring-blue-300"
                  placeholder="Mot de Passe (laisser vide si inchangé)"
                />
              </div>

              <!-- Agency Select Dropdown -->
              <div class="md:col-span-5">
                <label for="agency">Agence</label>
                <select v-model="form.agence_id" id="agence" class="w-full h-10 px-4 mt-1 border rounded bg-gray-50">
                  <option value="">Choisir une agence</option>
                  <option v-for="agence in agences" :key="agence.id" :value="agence.id">
                    {{ agence.name }}
                  </option>
                </select>
              </div>

              <div class="md:col-span-5">
                <label for="type">Rôle de l'utilisateur</label>
                <select
                  v-model="form.type"
                  id="type"
                  class="w-full h-10 px-4 mt-1 border rounded bg-gray-50 focus:ring focus:ring-blue-300"
                >
                  <option value="">Choisir</option>
                  <option value="5">Administrateur</option>
                  <option value="4">Manager</option>
                  <option value="3">Chef De Projet</option>
                  <option value="0">Consultant Technique</option>
                  <option value="1">Consultant Fonctionnel</option>
                  <option value="2">Consultant FullStack</option>
                </select>
              </div>
            </div>

            <div class="mt-4 text-center md:col-span-5">
              <div class="inline-flex items-center justify-between space-x-6">
                <button
                  @click="toggleCompetences"
                  v-if="!showCompetences"
                  class="px-4 py-2 font-bold text-white bg-blue-500 rounded left-6 hover:bg-blue-700"
                >
                  Voir Compétences
                </button>
                <button
                  @click="toggleCompetences"
                  v-if="showCompetences"
                  class="px-4 py-2 font-bold text-white bg-blue-500 rounded left-6 hover:bg-blue-700"
                >
                  Fermer Compétences
                </button>
                <router-link to="/listPs">
                  <button
                    class="px-4 py-2 font-bold text-white bg-blue-500 rounded hover:bg-blue-700"
                  >
                    Retour
                  </button>
                </router-link>

                <button
                  @click="updateUser"
                  class="px-4 py-2 font-bold text-white bg-blue-500 rounded hover:bg-blue-700"
                >
                  Enregistrer
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>

    

      <!-- Modal pour ajouter une compétence -->
      <transition name="modal">
        <div
          v-if="showAddCompetenceModal"
          class="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50"
        >
          <div class="p-4 bg-white rounded shadow-lg w-96">
            <h2 class="text-lg font-bold">Ajouter une Compétence</h2>
            <select
              v-model="newCompetence.domaine_id"
              class="w-full h-10 mt-2 border rounded bg-gray-50 focus:ring focus:ring-blue-300"
            >
              <option disabled selected>Choisissez un domaine</option>
              <option
                v-for="domaine in domaines"
                :key="domaine.id"
                :value="domaine.id"
              >
                {{ domaine.nom }} - {{ domaine.niveau_competence.niveau}}
              </option>
            </select>
            <div class="flex items-center justify-center space-x-4">
              <button
                @click="addCompetence"
                class="px-4 py-2 mt-4 text-white transition duration-200 bg-blue-500 rounded hover:bg-blue-700"
              >
                Ajouter
              </button>
              <button
                @click="showAddCompetenceModal = false"
                class="px-4 py-2 mt-4 text-black transition duration-200 border rounded hover:bg-gray-300"
              >
                Annuler
              </button>
            </div>
          </div>
        </div>
      </transition>

      <!-- Bouton pour ajouter les compétences -->
      <button
        @click="showAddCompetenceModal = true"
        class="absolute top-0 px-4 py-2 mt-4 text-white transition duration-200 bg-green-500 rounded left-6 hover:bg-green-700"
      >
        Ajouter Compétence
      </button>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      id: this.$route.params.id,
      form: {
        name: '',
        email: '',
        password: '',
        domaine_id: null,
        niveau_competence_id: null,
        type: null,
        agence_id: null,
      },
      agences: [],
      domaines: [],
      showCompetences: false,
      showAddCompetenceModal: false,
      newCompetence: {
        domaine_id: null,
        niveau_competence_id: null,
      },
      errorMessage: '',
      user: '',
    };
  },
  mounted() {
    this.fetchDomaines();
    this.fetchUserData();
    this.fetchAgencies();
  },
  methods: {
    fetchAgencies() {
      axios.get('http://127.0.0.1:8000/api/agences', {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem('token')}`
        }
      })
      .then(response => {
        // Ensure the API returns 'agences' and populate agencies
        this.agences = response.data.agences || [];
      })
      .catch(error => {
        console.error('Error fetching agencies:', error);
      });
    },
    async fetchDomaines() {
      try {
        const response = await axios.get('http://127.0.0.1:8000/api/domaines-competence', {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem('token')}`
          }
        });
        this.domaines = response.data;
      } catch (error) {
        console.error('Erreur lors du chargement des domaines:', error);
      }
    },
    async fetchUserData() {
      try {
        const response = await axios.get(`http://127.0.0.1:8000/api/users/${this.id}`, {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
        this.form = { ...response.data };
        this.user = response.data;
        this.errorMessage = '';
      } catch (error) {
        console.error('Erreur lors de la récupération des données de l\'utilisateur:', error);
        this.errorMessage = 'Utilisateur non trouvé. Veuillez vérifier l\'ID.';
      }
    },
    async addCompetence() {
      try {
        // Find the domain object that matches the selected domain ID
        const domaineToAdd = this.domaines.find(
          (domaine) => domaine.id === this.newCompetence.domaine_id
        );

        // Set the niveau_competence_id property of the newCompetence object based on the selected domain
        this.newCompetence.niveau_competence_id = domaineToAdd.niveau_competence.id;

        // Check if the selected domain is already added to the user's competences
        const alreadyAdded = this.user.domaines_competence.some(
          (competence) => competence.domaine_id === this.newCompetence.domaine_id
        );

        // If the selected domain is already added, do nothing and return
        if (alreadyAdded) return;

        // Send a POST request to the server to add the new competence
        await axios.post(
          `http://127.0.0.1:8000/api/users/${this.user.id}/competence`,
          {
            // Send the selected domain ID and level ID in the request body
            domaine_competence_id: this.newCompetence.domaine_id,
            niveau_competence_id: this.newCompetence.niveau_competence_id,
          },
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );

        // If the competence is added successfully, update the local user object
        this.user.domaines_competence.push({
          domaine_nom: domaineToAdd.nom,
          user_domaine_competence_id: this.newCompetence.domaine_id,
          niveau_competence: {
            id: this.newCompetence.niveau_competence_id,
            // Add other properties of the niveau_competence object here if needed
          },
        });

        // Close the modal and reset the selected domain and level
        this.showAddCompetenceModal = false;
        this.newCompetence.domaine_id = null;
        this.newCompetence.niveau_competence_id = null;
      } catch (error) {
        // If there's an error, log it to the console
        console.error("Erreur lors de l'ajout de la compétence:", error);
      }
    },
    async deleteCompetence(userDomaineCompetenceId) {
      try {
        await axios.delete(
          `http://127.0.0.1:8000/api/user-domaine-competence/${userDomaineCompetenceId}`,
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );

        this.user.domaines_competence = this.user.domaines_competence.filter(
          (competence) =>
            competence.user_domaine_competence_id !== userDomaineCompetenceId
        );
      } catch (error) {
        console.error("Erreur lors de la suppression de la compétence:", error);
      }
    },
    async updateUser() {
      try {
        const response = await axios.post(
          `http://127.0.0.1:8000/api/users/${this.id}`,
          this.form,
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );

        alert("Utilisateur mis à jour avec succès:", response.data);
        this.$router.push('/listPs');
        this.errorMessage = "";
      } catch (error) {
        console.error("Erreur lors de la mise à jour de l'utilisateur:", error);
        this.errorMessage = "Erreur lors de la mise à jour de l'utilisateur. Veuillez réessayer.";
      }
    },
    toggleCompetences() {
      this.showCompetences = !this.showCompetences;
    },
  },
};
</script>

<style scoped>
/* Ajoutez vos styles ici */
.slide-fade-enter-active {
  transition: all 0.8s ease;
}
.slide-fade-leave-active {
  transition: all 0.5s ease;
}
.slide-fade-enter, .slide-fade-leave-to /* .slide-fade-leave-active in <2.1.8 */ {
  transform: translateX(100%);
  opacity: 0;
}
.modal-enter-active, .modal-leave-active {
  transition: opacity 0.5s;
}
.modal-enter, .modal-leave-to /* .modal-leave-active in <2.1.8 */ {
  opacity: 0;
}
</style>
