<template>
    <div class="fixed top-0 left-0 flex items-center justify-center w-full h-full py-10 bg-black bg-opacity-50">
      <div class="w-full max-w-xl max-h-full overflow-y-auto bg-white sm:rounded-2xl">
        <div class="w-full">
          <div class="m-8 my-20 max-w-[400px] mx-auto">
            <div class="mb-8">
              <h1 class="mb-4 text-3xl font-extrabold">Réinitialiser votre mot de passe</h1>
              <p class="text-gray-600">Veuillez entrer votre nouveau mot de passe ci-dessous.</p>
            </div>
            <form @submit.prevent="resetPassword">
              <div class="space-y-4">
                <!-- Email -->
                <div>
                  <label for="email" class="block text-sm font-medium text-gray-700">Email</label>
                  <input
                    v-model="email"
                    id="email"
                    type="email"
                    class="block w-full p-2 mt-1 border rounded-md"
                    placeholder="Votre email"
                    required
                  />
                </div>
  
                <!-- Nouveau mot de passe -->
                <div>
                  <label for="password" class="block text-sm font-medium text-gray-700">Nouveau mot de passe</label>
                  <input
                    v-model="password"
                    id="password"
                    type="password"
                    class="block w-full p-2 mt-1 border rounded-md"
                    placeholder="Nouveau mot de passe"
                    required
                  />
                </div>
  
                <!-- Confirmation du mot de passe -->
                <div>
                  <label for="password_confirmation" class="block text-sm font-medium text-gray-700">Confirmez le mot de passe</label>
                  <input
                    v-model="password_confirmation"
                    id="password_confirmation"
                    type="password"
                    class="block w-full p-2 mt-1 border rounded-md"
                    placeholder="Confirmez le mot de passe"
                    required
                  />
                </div>
  
                <!-- Bouton Réinitialiser le mot de passe -->
                <button type="submit" class="w-full p-3 font-semibold text-white bg-black rounded-full">
                  Réinitialiser le mot de passe
                </button>
  
                <!-- Affichage des erreurs ou du succès -->
                <div v-if="errorMessage" class="mt-4 text-red-600">
                  {{ errorMessage }}
                </div>
                <div v-if="successMessage" class="mt-4 text-green-600">
                  {{ successMessage }}
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        email: '',
        password: '',
        password_confirmation: '',
        token: '', // Le token de réinitialisation doit être inclus
        errorMessage: null,
        successMessage: null,
      };
    },
    created() {
      // Récupère le token depuis l'URL
      this.token = this.$route.query.token;
    },
    methods: {
      async resetPassword() {
        try {
          const response = await axios.post('http://localhost/api/password/reset', {
            token: this.token,
            email: this.email,
            password: this.password,
            password_confirmation: this.password_confirmation,
          });
  
          this.successMessage = response.data.message;
          this.errorMessage = null;
        } catch (error) {
          if (error.response) {
            this.errorMessage = error.response.data.message;
          } else {
            this.errorMessage = "Une erreur s'est produite. Veuillez réessayer.";
          }
          this.successMessage = null;
        }
      },
    },
  };
  </script>
  
  <style scoped>
  /* Ajoute ici des styles personnalisés si nécessaire */
  </style>
  