<template>
  <div class="max-w-4xl p-6 mx-auto bg-white rounded-lg shadow-md">
    <h2 class="mb-4 text-2xl font-bold">
      {{ isEdit ? "Modifier un Domaine" : "Ajouter un Domaine" }}
    </h2>

    <form @submit.prevent="saveDomaine" class="space-y-4">
      <div>
        <label class="block text-sm font-medium text-gray-700">Nom du Domaine</label>
        <input
          v-model="domaineForm.nom"
          required
          placeholder="Nom du domaine"
          class="block w-full p-2 mt-1 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500"
        />
      </div>

      <div>
        <label class="block text-sm font-medium text-gray-700"
          >Niveau de Compétence</label
        >
        <select
          v-model="domaineForm.niveau_competence_id"
          required
          class="block w-full p-2 mt-1 border border-gray-300 rounded-md shadow-sm focus:ring-blue-500 focus:border-blue-500"
        >
          <option value="" disabled selected>Sélectionner un niveau</option>
          <option v-for="niveau in niveaux" :value="niveau.id" :key="niveau.id">
            {{ niveau.type }} {{ niveau.niveau }}
          </option>
        </select>
      </div>

      <button
        type="submit"
        class="w-full py-2 font-bold text-white transition duration-200 bg-blue-600 rounded hover:bg-blue-700"
      >
        {{ isEdit ? "Modifier" : "Ajouter" }}
      </button>
    </form>

    <!-- Liste des domaines dans un tableau -->
    <h3 class="mt-8 text-xl font-semibold">Liste des Domaines</h3>
    <table class="min-w-full mt-4 bg-gray-100 rounded-md shadow">
      <thead>
        <tr class="text-white bg-blue-600">
          <th class="px-4 py-2 text-left">Domaine</th>
          <th class="px-4 py-2 text-left">Niveau de Compétence</th>
          <th class="px-4 py-2 text-left">Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="domaine in domaines"
          :key="domaine.id"
          class="border-b hover:bg-gray-200"
        >
          <td class="px-4 py-2">{{ domaine.nom }}</td>
          <td class="px-4 py-2">
            <span v-if="domaine.niveau_competence">
              {{ domaine.niveau_competence.type }} {{ domaine.niveau_competence.niveau }}
            </span>
            <span v-else>Aucun niveau associé</span>
          </td>
          <td class="px-4 py-2">
            <button @click="editDomaine(domaine)" class="text-blue-600 hover:underline">
              Modifier
            </button>
            <button
              @click="deleteDomaine(domaine.id)"
              class="ml-4 text-red-600 hover:underline"
            >
              Supprimer
            </button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- Bouton flottant en coin droit -->
    <router-link
      to="/"
      class="absolute right-4 bottom-4 bg-blue-600 text-white font-bold py-2 px-4 rounded-full shadow-lg hover:bg-blue-700 transition duration-200"
    >
      Retour
    </router-link>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      domaines: [],
      niveaux: [], // Pour stocker les niveaux de compétence
      domaineForm: { nom: "", niveau_competence_id: "" },
      isEdit: false,
      editDomaineId: null,
    };
  },
  methods: {
    fetchDomaines() {
      axios
        .get("http://127.0.0.1:8000/api/domaines-competence", {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        })
        .then((response) => {
          this.domaines = response.data;
        });
    },
    fetchNiveaux() {
      axios
        .get("http://127.0.0.1:8000/api/niveaux-competence", {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        })
        .then((response) => {
          this.niveaux = response.data;
        });
    },
    saveDomaine() {
      const url = this.isEdit
        ? `http://127.0.0.1:8000/api/domaines-competence/${this.editDomaineId}`
        : "http://127.0.0.1:8000/api/domaines-competence";
      const method = this.isEdit ? "put" : "post";

      axios[method](url, this.domaineForm, {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      }).then(() => {
        this.fetchDomaines();
        this.resetForm();
      });
    },
    editDomaine(domaine) {
      this.domaineForm = {
        nom: domaine.nom,
        niveau_competence_id: domaine.niveau_competence.id,
      };
      this.isEdit = true;
      this.editDomaineId = domaine.id;
    },
    deleteDomaine(id) {
      axios
        .delete(`http://127.0.0.1:8000/api/domaines-competence/${id}`, {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        })
        .then(() => {
          this.fetchDomaines();
        });
    },
    resetForm() {
      this.domaineForm = { nom: "", niveau_competence_id: "" };
      this.isEdit = false;
      this.editDomaineId = null;
    },
  },
  mounted() {
    this.fetchDomaines();
    this.fetchNiveaux(); // Pour récupérer les niveaux au chargement
  },
};
</script>

<style scoped>
/* Ajoutez des styles personnalisés ici si nécessaire */
</style>
