<template>
  <div class="p-4">
    <!-- Navigation pour changer de vue mois/année -->
    <div class="flex items-center justify-between mb-4">
      <h2 class="text-2xl font-bold">{{ displayPeriod }}</h2>
      <div>
        <button
          @click="prevPeriod"
          class="px-4 py-2 text-white bg-blue-500 rounded shadow-lg text-[10px]"
        >
          Précédent
        </button>
        <button
          @click="resetToToday"
          class="px-4 py-2 ml-4 text-white bg-green-500 rounded shadow-lg text-[10px]"
        >
          Aujourd'hui
        </button>
        <button
          @click="nextPeriod"
          class="px-4 py-2 ml-4 text-white bg-blue-500 rounded shadow-lg text-[10px]"
        >
          Suivant
        </button>
      </div>
    </div>

   

    <!-- Conteneur principal pour le calcul du bench et la recherche -->
    <div class="flex items-center justify-between mb-2">
    
<!-- Formulaire pour le calcul du BANGE -->
    <div class="mb-4">
      <h3 class="mb-0 text-lg font-semibold text-gray-800 text-[10px]">
        Calcul du Bench
      </h3>
      <div class="flex items-center p-4 space-x-6 rounded-lg shadow-md bg-gray-50">
        <div class="flex items-center space-x-2">
          <label
            for="bange-start-date"
            class="text-sm font-medium text-gray-700 text-[10px]"
            >Date de début:</label
          >
          <input
            id="bange-start-date"
            v-model="bangeStartDate"
            type="date"
            class="p-2 border border-gray-300 rounded-md focus:ring focus:ring-blue-300 text-[10px]"
          />
        </div>
        <div class="flex items-center space-x-2">
          <label
            for="bange-end-date"
            class="text-sm font-medium text-gray-700 text-[10px]"
            >Date de fin:</label
          >
          <input
            id="bange-end-date"
            v-model="bangeEndDate"
            type="date"
            class="p-2 border border-gray-300 rounded-md focus:ring focus:ring-blue-300 text-[10px]"
          />
        </div>
        <button
          @click="calculateAndDisplayBANGE"
          class="px-4 py-2 text-[10px] text-white bg-blue-500 rounded shadow-lg hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50"
        >
          Calculer le Bench
        </button>
      </div>
    </div>

    <!-- Modal pour afficher les résultats du BANGE -->
    <div
      v-if="isBANGEModalOpen"
      class="fixed inset-0 z-[70] flex items-center justify-center bg-black bg-opacity-50"
    >
      <div class="w-full max-w-md p-6 bg-white rounded-lg">
        <h2 class="mb-4 text-xl font-bold">Résultats du Bench</h2>
        <div class="overflow-y-auto max-h-60">
          <table class="w-full">
            <thead>
              <tr>
                <th class="px-4 py-2 font-semibold text-left bg-gray-100">Date</th>
                <th class="px-4 py-2 font-semibold text-right bg-gray-100">BANGE</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="result in bangeResults" :key="result.date" class="border-b">
                <td class="px-4 py-2">{{ result.date }}</td>
                <td class="px-4 py-2 text-right">{{ result.bange }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <button
          @click="closeBANGEModal"
          class="px-4 py-2 mt-4 text-white bg-blue-500 rounded hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50"
        >
          Fermer
        </button>
      </div>
    </div>
    
      <!-- Formulaire de recherche -->
      <div class="mb-4">
        <h3 class="mb-0 text-lg font-semibold text-gray-800 text-[10px]">
          Recherche TNF
        </h3>
        <div class="flex items-center p-4 space-x-6 rounded-lg shadow-md bg-gray-50">
          <div class="flex items-center space-x-2">
            <label
              for="start-search"
              class="text-sm font-medium text-gray-700 text-[10px]"
              >Date de début:</label
            >
            <input
              id="start-search"
              v-model="searchStartDate"
              type="date"
              class="p-2 border border-gray-300 rounded-md focus:ring focus:ring-blue-300 text-[10px]"
            />
          </div>
          <div class="flex items-center space-x-2">
            <label for="end-search" class="text-sm font-medium text-gray-700 text-[10px]"
              >Date de fin:</label
            >
            <input
              id="end-search"
              v-model="searchEndDate"
              type="date"
              class="p-2 border border-gray-300 rounded-md focus:ring focus:ring-blue-300 text-[10px]"
            />
          </div>
          <button
            @click="clearDateFilters"
            class="px-4 py-2 text-[10px] text-white bg-red-500 rounded shadow-lg hover:bg-red-600 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-opacity-50"
          >
            Effacer les filtres
          </button>
        </div>
      </div>
    </div>
   <div>
      <!-- Conteneur principal pour les indicateurs  -->
      <div className=" border-t pt-2 w-[400px] ">
        <h4 className="font-bold mb-2">Légende</h4>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-2">
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-orange-400 rounded"></div>
            <span class="text-[9px]">Client</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-yellow-400 rounded"></div>
            <span class="text-[9px]">Prévision</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-sky-400 rounded"></div>
            <span class="text-[9px]">Voyage</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-blue-300 rounded"></div>
            <span class="text-[9px]">Férié/Congé</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-blue-500 rounded"></div>
            <span class="text-[9px]">Formation</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-red-400 rounded"></div>
            <span class="text-[9px]">Récupération</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-purple-600 rounded"></div>
            <span  class="text-[9px]">Congé maladie</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 bg-sky-600 rounded"></div>
            <span  class="text-[9px]">Université</span>
          </div>
        </div>
      </div>
       <!-- Basculer entre les vues Mois et Année -->
    <div class="flex justify-end mb-4">
      <button
        @click="viewMode = 'month'"
        :class="{ 'bg-blue-500 text-white': viewMode === 'month' }"
        class="px-4 py-2 transition-colors duration-200 rounded shadow-lg text-[10px]"
      >
        Mois
      </button>
      <button
        @click="viewMode = 'year'"
        :class="{ 'bg-blue-500 text-white': viewMode === 'year' }"
        class="px-4 py-2 ml-2 transition-colors duration-200 rounded shadow-lg text-[10px]"
      >
        Année
      </button>
    </div>
   </div>
    <!-- Vue Mensuelle -->
    <div v-if="viewMode === 'month'" class="overflow-x-auto overflow-y-scroll h-[400px]">
      <table class="min-w-full border border-gray-300 table-auto">
        <thead class="sticky z-50 text-white bg-blue-500">
          <tr class="sticky z-50 bg-blue-500">
            <th
              class="sticky left-0 w-1/4 p-2 text-[9px] z-[70] bg-blue-500 rounded-l-lg"
            >
              Ressources
            </th>
            <th
              v-for="(day, index) in periodDays"
              :key="index"
              class="sticky top-0 p-2 border-b bg-blue-500 z-50 border-gray-300 w-[50px] text-[9px]"
            >
              {{ getDayLabel(index, day) }}
            </th>
          </tr>
        </thead>
        <tbody>
          <!-- Resource rows -->
          <tr v-for="resource in filteredResources" :key="resource.name">
            <td class="sticky left-0 p-1 bg-white text-[10px] z-50">
              {{ resource.name }}
            </td>
            <td
              v-for="(day, index) in periodDays"
              :key="index"
              class="p-1 border-b border-gray-300 w-[50px] text-[9px]"
              :class="{
                'bg-red-400': isJourFerie(day),
                'bg-gray-300': isWeekend(day).isWeekend,
              }"
              @click="openEventModal(day, resource.name)"
            >
              <!-- Holiday Display -->
              <div v-if="isJourFerie(day)" class="text-xs font-bold text-white">
                <div class="w-[50px] h-6 p-1 text-[6px] text-white bg-red-400 rounded">
                  {{ getJourFerieName(day, monthIndex) }}
                </div>
              </div>

              <!-- Weekend Display -->
              <div
                v-else-if="isWeekend(day).isWeekend"
                class="w-full h-full p-1 text-[9px] text-black bg-gray-300 rounded"
              >
                <template v-if="filteredEventsByDay[resource.name][day].length">
                  <div
                    v-for="event in filteredEventsByDay[resource.name][day]"
                    :key="event.id"
                    class="relative w-[50px] h-6 p-1 text-[9px] text-center rounded cursor-pointer"
                    :class="getClientColor(event.color)"
                    @mouseover="showComment(event, day)"
                    @mouseleave="hideComment"
                  >
                    {{ event.clientAbbreviation }}
                  </div>
                </template>
                <template v-else><p class="text-[9px]">weekend</p></template>
              </div>

              <!-- Normal Day Display (not holiday, not weekend) -->
              <div v-else>
                <template v-if="filteredEventsByDay[resource.name][day].length">
                  <div
                    v-for="event in filteredEventsByDay[resource.name][day]"
                    :key="event.id"
                    class="relative w-[50px] h-6 p-1 text-[9px] text-center rounded cursor-pointer"
                    :class="getClientColor(event.color)"
                    @mouseover="showComment(event, day)"
                    @mouseleave="hideComment"
                  >
                    {{ event.clientAbbreviation }}
                    <div
                      v-if="hoveredEvent === event && hoveredDay === day"
                      class="absolute z-10 p-2 text-sm text-white whitespace-normal bg-gray-800 rounded shadow-lg"
                      style="
                        width: 200px;
                        bottom: 100%;
                        left: 50%;
                        transform: translateX(-50%);
                      "
                    >
                      {{ event.name }}
                    </div>
                  </div>
                </template>
                <!-- TNF for normal days without events -->
                <template v-else>
                  <div
                    v-if="!isJourFerie(day)"
                    class="h-6 p-1 text-[9px] text-white bg-green-400 rounded cursor-pointer hover:bg-green-600"
                    :class="{
                      'border-2 border-blue-600': isHighlightedTNF(resource.name, day),
                    }"
                  >
                    TNF
                  </div>
                </template>
              </div>
            </td>
          </tr>

          <!-- Bench Row for Weekly BANGE -->
          <tr class="hidden">
            <td class="sticky left-0 p-1 bg-white text-[10px] font-bold">Bench</td>
            <td
              v-for="(day, index) in periodDays"
              :key="index"
              class="p-1 border-b border-gray-300 w-[50px] text-sm"
            >
              <div v-if="index % 7 === 3">
                <!-- Assuming BANGE is calculated weekly -->
                <span>{{ getWeeklyBANGE(day).bange }}</span>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- Vue Annuelle -->
    <div v-else-if="viewMode === 'year'" class="overflow-x-auto">
      <table class="min-w-full border border-gray-900 table-auto">
        <thead class="text-white bg-blue-500">
          <tr>
            <th class="sticky left-0 w-1/4 p-1 bg-blue-500 rounded-l-lg">Ressources</th>
            <th
              v-for="month in monthsOfYear"
              :key="month"
              class="p-1 border border-gray-300"
            >
              <div>{{ month }}</div>
              <div class="flex row-auto gap-1">
                <div
                  v-for="day in getDaysInMonth(monthsOfYear.indexOf(month))"
                  :key="`${month}-${day}`"
                  class="text-[9px] p-1 w-[50px] mr-[8px]"
                >
                  {{
                    getDayAbbreviation(
                      new Date(currentYear, monthsOfYear.indexOf(month), day)
                    )
                  }}
                  {{ String(day).padStart(2, "0") }}
                </div>
              </div>
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="resource in filteredResources" :key="resource.name">
            <td class="sticky left-0 z-50 p-1 bg-white border text-[9px]">
              {{ resource.name }}
            </td>
            <td
              v-for="(month, monthIndex) in getDaysInYear()"
              :key="monthIndex"
              class="p-1 border border-gray-300 text-[9px]"
            >
              <div class="flex gap-1">
                <div
                  v-for="day in month.days"
                  :key="day"
                  class="p-1 text-[9px] text-center rounded-lg"
                  @click="openEventModal(day, resource.name, monthIndex)"
                >
                  <!-- Ajout de la condition pour les jours fériés -->
                  <div v-if="isJourFerie(day, monthIndex)">
                    <div
                      class="w-[50px] h-6 p-1 text-[6px] text-white bg-red-400 rounded"
                    >
                      {{ getJourFerieName(day, monthIndex) }}
                    </div>
                  </div>
                  <div v-else-if="isWeekend(day, monthIndex).isWeekend">
                    <div
                      class="w-[50px] h-6 p-1 text-[9px] text-center bg-gray-300 rounded"
                    >
                      <template
                        v-if="getEventsForMonth(resource.name, monthIndex, day).length"
                      >
                        <div
                          v-for="event in getEventsForMonth(
                            resource.name,
                            monthIndex,
                            day
                          )"
                          :key="event.id"
                          class="relative text-center rounded cursor-pointer w-[50px] h-6 p-1 text-[9px]"
                          :class="getClientColor(event.color)"
                          @mouseover="showComment(event, day)"
                          @mouseleave="hideComment"
                        >
                          {{ event.clientAbbreviation }}
                          <div
                            v-if="hoveredEvent === event && hoveredDay === day"
                            class="absolute z-10 p-1 text-[9px] text-white whitespace-normal bg-gray-800 rounded shadow-lg"
                            style="
                              width: 200px;
                              bottom: 100%;
                              left: 50%;
                              transform: translateX(-50%);
                            "
                          >
                            {{ event.name }}
                          </div>
                        </div>
                      </template>
                      <template v-else>weekend</template>
                    </div>
                  </div>
                  <div v-else>
                    <template
                      v-if="getEventsForMonth(resource.name, monthIndex, day).length"
                    >
                      <div
                        v-for="event in getEventsForMonth(resource.name, monthIndex, day)"
                        :key="event.id"
                        class="relative w-[50px] h-6 p-1 text-[9px] text-center rounded cursor-pointer"
                        :class="getClientColor(event.color)"
                        @mouseover="showComment(event, day)"
                        @mouseleave="hideComment"
                      >
                        {{ event.clientAbbreviation }}
                        <div
                          v-if="hoveredEvent === event && hoveredDay === day"
                          class="absolute z-10 w-[50px] h-6 p-1 text-[9px] text-white whitespace-normal bg-gray-800 rounded shadow-lg"
                          style="
                            width: 200px;
                            bottom: 100%;
                            left: 50%;
                            transform: translateX(-50%);
                          "
                        >
                          {{ event.name }}
                        </div>
                      </div>
                    </template>
                    <template v-else>
                      <div
                        v-if="!isJourFerie(day, monthIndex)"
                        class="w-[50px] h-6 p-1 text-[9px] text-white bg-green-400 rounded cursor-pointer hover:bg-green-600"
                        :class="{
                          'border-2 border-blue-600': isHighlightedTNF(
                            resource.name,
                            day,
                            monthIndex
                          ),
                        }"
                      >
                        TNF
                      </div>
                    </template>
                  </div>
                </div>
              </div>
            </td>
          </tr>
          <!-- Bench Row pour la vue annuelle -->
          <tr class="hidden">
            <td class="sticky left-0 p-1 bg-white text-[9px] font-bold">Bench</td>
            <td
              v-for="(month, monthIndex) in getDaysInYear()"
              :key="monthIndex"
              class="p-1 border border-gray-300"
            >
              <div class="flex gap-[40px]">
                <div
                  v-for="day in month.days"
                  :key="day"
                  class="p-1 text-[9px] text-center"
                >
                  <!-- Afficher le BANGE pour chaque mercredi en vérifiant directement le jour -->
                  <div
                    v-if="new Date(currentYear, monthIndex, day).getDay() === 3"
                    class="w-[50px] text-center text-sm"
                  >
                    {{ getWeeklyBANGE(day, monthIndex).bange }}
                  </div>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>

  <!-- Modal pour ajouter/modifier un événement -->
  <div
    v-if="isEventModalOpen"
    class="fixed inset-0 z-50 flex items-center justify-center p-6 bg-black bg-opacity-50 backdrop-blur-sm"
    @click.self="closeEventModal"
  >
    <div
      class="w-full max-w-2xl p-6 transition-transform duration-300 ease-in-out transform scale-75 bg-white rounded-lg shadow-lg"
    >
      <h3 class="mb-4 text-lg font-semibold text-gray-800">
        Ressource : {{ selectedEvent.resourceName }}
      </h3>

      <div class="mt-4">
        <label for="client" class="block text-sm font-medium text-gray-700"
          >Client:</label
        >
        <div>
          <!-- Barre de recherche pour filtrer les clients -->
          <input
            type="text"
            v-model="clientSearch"
            placeholder="Rechercher un client"
            class="w-full p-2 mb-2 border border-gray-300 rounded-md focus:ring focus:ring-blue-500 focus:border-blue-500"
          />

          <!-- Sélecteur de clients -->
          <select
            id="client"
            v-model="selectedClientId"
            @change="updateSelectedClient"
            class="w-full p-3 transition duration-150 ease-in-out border border-gray-300 rounded-md focus:ring focus:ring-blue-500 focus:border-blue-500"
          >
            <option value="">Sélectionnez un client</option>
            <option v-for="client in filteredClients" :key="client.id" :value="client.id">
              {{ client.nom }}
            </option>
          </select>
        </div>
      </div>

      <div class="mt-4">
        <label for="comment" class="block text-sm font-medium text-gray-700"
          >Commentaire:</label
        >
        <textarea
          id="comment"
          v-model="comment"
          class="w-full p-3 transition duration-150 ease-in-out border border-gray-300 rounded-md focus:ring focus:ring-blue-500 focus:border-blue-500"
          placeholder="Entrez un commentaire"
        ></textarea>
      </div>

      <div class="flex justify-between mt-4">
        <div class="w-full pr-2">
          <label for="start-date" class="block text-sm font-medium text-gray-700"
            >Date de début:</label
          >
          <input
            id="start-date"
            type="date"
            v-model="startDate"
            class="w-full p-3 transition duration-150 ease-in-out border border-gray-300 rounded-md focus:ring focus:ring-blue-500 focus:border-blue-500"
          />
        </div>
        <div class="w-full pl-2">
          <label for="end-date" class="block text-sm font-medium text-gray-700"
            >Date de fin:</label
          >
          <input
            id="end-date"
            type="date"
            v-model="endDate"
            class="w-full p-3 transition duration-150 ease-in-out border border-gray-300 rounded-md focus:ring focus:ring-blue-500 focus:border-blue-500"
          />
        </div>
      </div>

      <div class="mt-4">
        <label for="selectedClientColor" class="block text-sm font-medium text-gray-700"
          >type de client</label
        >
        <input type="hidden" v-model="selectedClientColor" />
      </div>
      <div class="flex space-x-4">
        <button
          v-if="selectedEvent.id"
          @click="updateEvent"
          class="w-full px-4 py-3 mt-6 text-white transition duration-150 ease-in-out bg-green-500 rounded-md hover:bg-green-600 focus:outline-none focus:ring focus:ring-green-300"
        >
          Mettre à jour l'intervention
        </button>

        <button
          v-else
          @click="saveEvent"
          class="w-full px-4 py-3 mt-6 text-white transition duration-150 ease-in-out bg-blue-500 rounded-md hover:bg-blue-600 focus:outline-none focus:ring focus:ring-blue-300"
        >
          Enregistrer l'intervention
        </button>

        <!-- Button to delete the event -->
        <button
          v-if="selectedEvent.id"
          @click="deleteEvent"
          class="w-full px-4 py-3 mt-6 text-white transition duration-150 ease-in-out bg-red-500 rounded-md hover:bg-red-600 focus:outline-none focus:ring focus:ring-red-300"
        >
          Supprimer l'intervention
        </button>

        <button
          @click="closeEventModal"
          class="w-full px-4 py-3 mt-6 text-white transition duration-150 ease-in-out bg-gray-500 rounded-md hover:bg-gray-600 focus:outline-none focus:ring focus:ring-gray-300"
        >
          Fermer
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { format } from "date-fns";
import axios from "axios";
export default {
  data() {
    return {
      currentYear: new Date().getFullYear(),
      currentMonth: new Date().getMonth(),
      monthsOfYear: [
        "Janvier",
        "Février",
        "Mars",
        "Avril",
        "Mai",
        "Juin",
        "Juillet",
        "Août",
        "Septembre",
        "Octobre",
        "Novembre",
        "Décembre",
      ],

      currentDay: new Date().getDate(),
      viewMode: "month",
      resources: [],
      events: [],
      selectedClientId: "",
      clientSearch: "", // Barre de recherche pour le filtre
      clients: [], // Assurez-vous que `clients` contient les données de vos clients
      joursFeries: [],
      bangeResults: [],
      isBANGEModalOpen: false,
      bangeStartDate: '',
      bangeEndDate: '',
      searchStartDate: "",
      searchEndDate: "",
      tnfSearchResults: [],
      isEventModalOpen: false,
      selectedEvent: {},
      hoveredEvent: null,
      hoveredDay: null,
      selectedClientColor: "",
      comment: "",
      startDate: "",
      endDate: "",
      user_id: null,
    };
  },
  computed: {
    filteredClients() {
      return this.clients.filter((client) =>
        client.nom.toLowerCase().includes(this.clientSearch.toLowerCase())
      );
    },
    displayPeriod() {
      if (this.viewMode === "month") {
        const firstDayOfMonth = new Date(this.currentYear, this.currentMonth, 1);
        return format(firstDayOfMonth, "MMMM yyyy");
      } else if (this.viewMode === "year") {
        const firstDayOfYear = new Date(this.currentYear, 0, 1);
        return format(firstDayOfYear, "yyyy");
      } else {
        return `${this.currentYear}`;
      }
    },
    formattedJoursFeries() {
      // Format the public holidays to only include the date in 'yyyy-MM-dd' format
      return this.joursFeries.map((jourFerie) => ({
        date: format(new Date(jourFerie.date_ferie), "yyyy-MM-dd"), // Keep the date format
        name: jourFerie.nom_ferie,
      }));
    },

    periodDays() {
      return this.viewMode === "month" ? this.getDaysInMonth() : this.getDaysInYear();
    },
    filteredResources() {
      if (!this.searchStartDate || !this.searchEndDate) {
        return this.resources;
      }

      const startDate = new Date(this.searchStartDate);
      const endDate = new Date(this.searchEndDate);

      return this.resources.filter((resource) => {
        const tnfDays = this.getTNFDaysInPeriod(resource, startDate, endDate);
        return tnfDays.length > 0;
      });
    },

    // Nouvelle propriété calculée pour récupérer les événements par jour et ressource
    filteredEventsByDay() {
      const filteredEvents = {};

      this.resources.forEach((resource) => {
        filteredEvents[resource.name] = {};

        this.periodDays.forEach((day) => {
          const dayDate = new Date(this.currentYear, this.currentMonth, day);
          filteredEvents[resource.name][day] = this.events.filter(
            (event) =>
              event.resource === resource.name && this.isEventOnDay(event, dayDate)
          );
        });
      });

      return filteredEvents;
    },
  },
  watch: {
    searchStartDate() {
      this.updateView();
    },
    searchEndDate() {
      this.updateView();
    },
  },

  mounted() {
    this.fetchResourcesAndEvents();
    this.fetchClients();
    this.fetchJoursFeries();
  },
  methods: {
    calculateBANGE(startDate, endDate) {
      const start = new Date(startDate);
      const end = new Date(endDate);
      let totalTNF = 0;
      let workingDays = 0;

      for (let d = new Date(start); d <= end; d.setDate(d.getDate() + 1)) {
        const dayOfWeek = d.getDay();
        if (
          dayOfWeek !== 0 &&
          dayOfWeek !== 6 &&
          !this.isJourFerie(d.getDate(), d.getMonth())
        ) {
          workingDays++;
          this.resources.forEach((resource) => {
            const events = this.getEventsForMonth(
              resource.name,
              d.getMonth(),
              d.getDate()
            );
            if (!events || events.length === 0) {
              totalTNF++;
            }
          });
        }
      }

      const etp = workingDays > 0 ? Math.min(workingDays, 5) : 0;
      const bange = etp > 0 ? (totalTNF / etp).toFixed(1) : 0;
      return bange;
    },

    calculateAndDisplayBANGE() {
      if (this.bangeStartDate && this.bangeEndDate) {
        const bangeResult = this.calculateBANGE(this.bangeStartDate, this.bangeEndDate);
        this.bangeResults.push({
          date: `${this.bangeStartDate} - ${this.bangeEndDate}`,
          bange: bangeResult,
        });
         this.isBANGEModalOpen = true;
      } else {
        alert("Veuillez sélectionner une date de début et une date de fin.");
      }
    },
     closeBANGEModal() {
      this.isBANGEModalOpen = false;
      this.bangeStartDate = '';
      this.bangeEndDate = '';
      this.bangeResults = [];
    },
    getDayAbbreviation(date) {
      const days = ["dim", "lun", "mar", "mer", "jeu", "ven", "sam"];
      return days[date.getDay()];
    },

    isWednesday(day, monthIndex) {
      const date = new Date(this.currentYear, monthIndex, day);
      return date.getDay() === 3; // 3 représente mercredi (0 = dimanche, 1 = lundi, etc.)
    },

    // Modifiez la méthode getWeeklyBANGE pour prendre en compte le monthIndex
    getWeeklyBANGE(day, monthIndex = null) {
      // Define start and end of the week based on `day`
      const weekStart = new Date(
        this.currentYear,
        monthIndex !== null ? monthIndex : this.currentMonth,
        day
      );
      const weekEnd = new Date(weekStart);
      weekEnd.setDate(weekStart.getDate() + 6);

      let totalTNF = 0;
      let workingDays = 0;

      for (let d = new Date(weekStart); d <= weekEnd; d.setDate(d.getDate() + 1)) {
        const dayOfWeek = d.getDay();
        if (
          dayOfWeek !== 0 &&
          dayOfWeek !== 6 &&
          !this.isJourFerie(d.getDate(), d.getMonth())
        ) {
          workingDays++;
          this.resources.forEach((resource) => {
            const events = this.getEventsForMonth(
              resource.name,
              d.getMonth(),
              d.getDate()
            );
            if (!events || events.length === 0) {
              totalTNF++;
            }
          });
        }
      }

      const etp = workingDays > 0 ? Math.min(workingDays, 5) : 0;
      const bange = etp > 0 ? (totalTNF / etp).toFixed(1) : 0;
      return { bange };
    },
    getJourFerieName(day, monthIndex = null) {
      const date = new Date(
        this.currentYear,
        monthIndex !== null ? monthIndex : this.currentMonth,
        typeof day === "number" ? day : new Date(day).getDate()
      );
      const formattedDate = format(date, "yyyy-MM-dd");
      const jourFerie = this.formattedJoursFeries.find(
        (jour) => jour.date === formattedDate
      );
      return jourFerie ? jourFerie.name : "Férié";
    },
    isJourFerie(day, monthIndex = null) {
      const date = new Date(
        this.currentYear,
        monthIndex !== null ? monthIndex : this.currentMonth,
        typeof day === "number" ? day : new Date(day).getDate()
      );
      const formattedDate = format(date, "yyyy-MM-dd");
      return this.formattedJoursFeries.some((jour) => jour.date === formattedDate);
    },

    calculateBusinessDays(startDate, endDate) {
      let count = 0;
      const currentDate = new Date(startDate);

      while (currentDate <= endDate) {
        const dayOfWeek = currentDate.getDay();
        if (dayOfWeek !== 0 && dayOfWeek !== 6) {
          count++;
        }
        currentDate.setDate(currentDate.getDate() + 1);
      }

      return count;
    },
    showComment(event, day) {
      this.hoveredEvent = event;
      this.hoveredDay = day;
    },

    hideComment() {
      this.hoveredEvent = null;
      this.hoveredDay = null;
    },
    // Remplacer la méthode searchTNF par updateView
    updateView() {
      if (this.searchStartDate && this.searchEndDate) {
        const startDate = new Date(this.searchStartDate);
        this.currentYear = startDate.getFullYear();
        this.currentMonth = startDate.getMonth();
      }
    },
    clearDateFilters() {
      this.searchStartDate = "";
      this.searchEndDate = "";
      this.updateView();
    },
    getTNFDaysInPeriod(resource, startDate, endDate) {
      const tnfDays = [];
      const resourceEvents = this.events.filter(
        (event) => event.resource === resource.name
      );

      for (let d = new Date(startDate); d <= endDate; d.setDate(d.getDate() + 1)) {
        // Ignorer les week-ends et les jours fériés
        if (
          this.isWeekend(d.getDate(), d.getMonth()).isWeekend ||
          this.isJourFerie(d.getDate())
        ) {
          continue;
        }

        const eventsOnDay = resourceEvents.filter((event) => this.isEventOnDay(event, d));
        if (eventsOnDay.length === 0) {
          tnfDays.push(new Date(d));
        }
      }

      return tnfDays;
    },

    isHighlightedTNF(resourceName, day, monthIndex) {
      if (!this.searchStartDate || !this.searchEndDate) return false;

      const resource = this.resources.find((r) => r.name === resourceName);
      if (!resource) return false;

      const date = new Date(
        this.currentYear,
        monthIndex !== undefined ? monthIndex : this.currentMonth,
        day
      );
      const startDate = new Date(this.searchStartDate);
      const endDate = new Date(this.searchEndDate);

      // Exclure le jour s'il est en dehors de la plage ou s'il est un jour férié
      if (date < startDate || date > endDate || this.isJourFerie(day)) return false;

      const tnfDays = this.getTNFDaysInPeriod(resource, startDate, endDate);

      return tnfDays.some(
        (tnfDay) =>
          tnfDay.getDate() === date.getDate() &&
          tnfDay.getMonth() === date.getMonth() &&
          tnfDay.getFullYear() === date.getFullYear()
      );
    },
    async fetchJoursFeries() {
      try {
        const response = await axios.get("http://127.0.0.1:8000/api/jours-feries", {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
        this.joursFeries = response.data; // Stocke les jours fériés
      } catch (error) {
        console.error("Erreur lors de la récupération des jours fériés:", error);
      }
    },
    async fetchClients() {
      const API_URL = "http://127.0.0.1:8000/api";
      try {
        const response = await axios.get(`${API_URL}/clients`, {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
        this.clients = response.data;
      } catch (error) {
        console.error("Erreur lors de la récupération des clients:", error);
      }
    },
    updateSelectedClient() {
      const selectedClient = this.clients.find(
        (client) => client.id === this.selectedClientId
      );
      if (selectedClient) {
        this.selectedClientColor = selectedClient.color;
      }
    },

    async saveEvent() {
      const API_URL = "http://127.0.0.1:8000/api";
      try {
        await axios.post(
          `${API_URL}/interventions`,
          {
            user_id: this.user_id,
            client_id: this.selectedClientId, // Assurez-vous d'utiliser l'ID du client ici
            commentaire: this.comment,
            date_debut: this.startDate,
            date_fin: this.endDate,
            color: this.selectedClientColor,
          },
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );

        await this.fetchResourcesAndEvents();
        this.closeEventModal();
      } catch (error) {
        console.error("Erreur lors de la sauvegarde de l'événement:", error);
        if (error.response) {
          console.error("Détails de l'erreur:", error.response.data);
          // Affichez un message d'erreur utilisateur ici si nécessaire
        }
      }
    },

    async updateEvent() {
      const API_URL = "http://127.0.0.1:8000/api";
      try {
        await axios.post(
          // Utilisez PUT pour les mises à jour
          `${API_URL}/interventions/${this.selectedEvent.id}`,
          {
            user_id: this.user_id,
            client_id: this.selectedClientId, // Assurez-vous d'utiliser l'ID du client ici
            commentaire: this.comment,
            date_debut: this.startDate,
            date_fin: this.endDate,
            color: this.selectedClientColor,
          },
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        );

        await this.fetchResourcesAndEvents();
        this.closeEventModal();
      } catch (error) {
        console.error("Erreur lors de la mise à jour de l'événement:", error);
        if (error.response) {
          console.error("Détails de l'erreur:", error.response.data);
          // Affichez un message d'erreur utilisateur ici si nécessaire
        }
      }
    },
    selectClient(clientId) {
      const client = this.clients.find((c) => c.id === clientId);
      if (client) {
        this.selectedClient = client.nom;
        this.selectedClientId = client.id;
        this.selectedClientColor = client.color; // Assurez-vous que cette ligne est présente
      } else {
        this.selectedClient = null;
        this.selectedClientId = null;
        this.selectedClientColor = "";
      }
    },

    openEventModal(day, resourceName, monthIndex = null) {
      let selectedDate;

      if (monthIndex !== null) {
        selectedDate = new Date(this.currentYear, monthIndex, day);
      } else {
        selectedDate = new Date(this.currentYear, this.currentMonth, day);
      }

      if (!isNaN(selectedDate.getTime())) {
        const headerDate = format(selectedDate, "yyyy-MM-dd");
        this.startDate = headerDate;

        const resource = this.resources.find((r) => r.name === resourceName);

        if (resource) {
          this.selectedEvent = {
            resourceName,
            resourceId: resource.id,
            day: this.startDate,
          };
          this.user_id = resource.id;

          const events =
            monthIndex !== null
              ? this.getEventsForMonth(resourceName, monthIndex, day)
              : this.filteredEventsByDay[resourceName][day];

          const event = events && events[0];

          if (event) {
            const intervention = resource.interventions.find((i) => i.id === event.id);
            console.log(intervention); // Ajoutez ceci pour vérifier ce que contient intervention
            if (intervention) {
              this.selectedClientId = intervention.client_id; // L'ID du client
              this.selectedClient = intervention.client_name; // Le nom du client
              this.comment = intervention.commentaire;
              this.startDate = format(new Date(intervention.date_debut), "yyyy-MM-dd");
              this.endDate = format(new Date(intervention.date_fin), "yyyy-MM-dd");
              (this.selectedClientColor = intervention.client_color),
                (this.selectedEvent.id = intervention.id); // Marquer comme événement existant
            }
          } else {
            // Réinitialiser pour un nouvel événement
            this.selectedClient = null;
            this.selectedClientId = null;
            this.comment = "";
            this.startDate = headerDate;
            this.endDate = "";
            this.selectedClientColor = ""; // Valeur par défaut
            this.selectedEvent.id = null; // Marquer comme nouvel événement
          }

          this.isEventModalOpen = true; // Ouvrir le modal
        }
      }
    },

    async deleteEvent() {
      const API_URL = "http://127.0.0.1:8000/api";
      try {
        await axios.delete(`${API_URL}/interventions/${this.selectedEvent.id}`, {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });

        await this.fetchResourcesAndEvents();
        this.closeEventModal();
      } catch (error) {
        console.error("Erreur lors de la suppression de l'événement:", error);
        if (error.response) {
          console.error("Détails de l'erreur:", error.response.data);
        }
      }
    },

    closeEventModal() {
      this.isEventModalOpen = false;
      this.selectedEvent = {};
      this.selectedClient = null;
      this.comment = "";
      this.startDate = "";
      this.endDate = "";
      this.selectedClientColor = "";
    },

    resetToToday() {
      const today = new Date();
      this.currentYear = today.getFullYear();
      this.currentMonth = today.getMonth();
      this.currentDay = today.getDate();
    },

    prevPeriod() {
      if (this.viewMode === "month") {
        this.currentMonth--;
        if (this.currentMonth < 0) {
          this.currentMonth = 11;
          this.currentYear--;
        }
      } else {
        this.currentYear--;
      }
      this.fetchJoursFeries(); // Ajoutez cette ligne
    },

    nextPeriod() {
      if (this.viewMode === "month") {
        this.currentMonth++;
        if (this.currentMonth > 11) {
          this.currentMonth = 0;
          this.currentYear++;
        }
      } else {
        this.currentYear++;
      }
      this.fetchJoursFeries(); // Ajoutez cette ligne
    },

    getDayLabel(index, day) {
      const dayDate = new Date(this.currentYear, this.currentMonth, day);
      return format(dayDate, "EEE dd");
    },

    getDaysInMonth(monthIndex = null) {
      const month = monthIndex !== null ? monthIndex : this.currentMonth;
      const daysInMonth = new Date(this.currentYear, month + 1, 0).getDate();
      return Array.from({ length: daysInMonth }, (_, i) => i + 1);
    },

    getDaysInYear() {
      const months = [];
      for (let month = 0; month < 12; month++) {
        const daysInMonth = new Date(this.currentYear, month + 1, 0).getDate();
        const days = Array.from({ length: daysInMonth }, (_, i) => i + 1);
        months.push({
          month: format(new Date(this.currentYear, month), "MMMM"),
          days,
        });
      }
      return months;
    },
    isWeekend(day, month = this.currentMonth) {
      const date = new Date(this.currentYear, month, day);
      const dayOfWeek = date.getDay();
      return { isWeekend: dayOfWeek === 0 || dayOfWeek === 6, dayOfWeek };
    },
    isEventOnDay(event, day) {
      const eventStart = new Date(event.startDate);
      const eventEnd = new Date(event.endDate);
      const dayOfWeek = day.getDay();
      const isWeekend = dayOfWeek === 0 || dayOfWeek === 6;

      const dayDate = new Date(day.getFullYear(), day.getMonth(), day.getDate());
      const eventStartDate = new Date(
        eventStart.getFullYear(),
        eventStart.getMonth(),
        eventStart.getDate()
      );
      const eventEndDate = new Date(
        eventEnd.getFullYear(),
        eventEnd.getMonth(),
        eventEnd.getDate()
      );

      if (isWeekend) {
        // Pour les week-ends, afficher uniquement les événements qui commencent un week-end
        return (
          (eventStart.getDay() === 0 || eventStart.getDay() === 6) &&
          dayDate >= eventStartDate &&
          dayDate <= eventEndDate
        );
      } else {
        // Pour les jours de semaine, afficher uniquement les événements qui commencent un jour de semaine
        return (
          eventStart.getDay() !== 0 &&
          eventStart.getDay() !== 6 &&
          dayDate >= eventStartDate &&
          dayDate <= eventEndDate
        );
      }
    },

    getEventsForMonth(resourceName, monthIndex, day) {
      const date = new Date(this.currentYear, monthIndex, day);
      return this.events.filter(
        (event) => event.resource === resourceName && this.isEventOnDay(event, date)
      );
    },

    async fetchResourcesAndEvents() {
      const API_URL = "http://127.0.0.1:8000/api";
      try {
        const response = await axios.get(`${API_URL}/interventions`, {
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
        const data = response.data;

        this.resources = data.map((user) => ({
          id: user.id, // Assuming the API returns an 'id' for each user
          name: user.name,
          interventions: user.interventions,
        }));

        this.events = data.flatMap((user) =>
          user.interventions.map((intervention) => ({
            id: intervention.id,
            name: intervention.commentaire,
            startDate: intervention.date_debut,
            endDate: intervention.date_fin,
            resource: user.name,
            color: intervention.client_color, // Utilisez la couleur du client
            resourceId: user.id, // Add the resource (user) ID to each event
            clientAbbreviation: intervention.client_abbreviation,
          }))
        );
      } catch (error) {
        console.error("Erreur lors de la récupération des données:", error);
      }
    },

    getClientColor(color) {
      if (!color || typeof color !== "number") {
        return "bg-blue-400"; // Couleur par défaut si la couleur n'est pas définie ou invalide
      }

      switch (color) {
        case 1:
          return "bg-orange-400"; // Client (Orange)
        case 2:
          return "bg-yellow-400"; // Prévision (Jaune)
        case 3:
          return "bg-sky-400"; // Voyage (Bleu clair)
        case 4:
          return "bg-blue-300"; // Férié/Congé (Bleu ciel)
        case 5:
          return "bg-blue-500"; // Formation (Bleu)
        case 6:
          return "bg-red-400"; // Récupération (Rouge)
        case 7:
          return "bg-purple-600"; // Congé maladie (Violet)
        case 8:
          return "bg-sky-600"; // Université (Sky)
        default:
          return "bg-blue-400";
      }
    },
  },
};
</script>

<style scoped>
.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

/* Ajoutez ces styles pour une meilleure apparence du commentaire */
.absolute {
  max-width: 200px;
  word-wrap: break-word;
  z-index: 100;
}
.tnf-event {
  width: 20px; /* ajuster la largeur pour qu'elle corresponde à la journée */
  max-width: 20px;
}
</style>
