import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue';
import LoginView from '../components/Auth/Login.vue';
import NavView from '../views/NavView.vue';
import AboutView from '../views/AboutView.vue';
import ForgotComponentView from '../components/Auth/ForgotComponent.vue';
import ResetPassword from '../components/Auth/ResetPassword.vue';
import ProjetView from '../views/StaffingView.vue';
import ProjectComponnent from '@/components/project/AddProject.vue';
import updateproject from '@/components/project/UpdateProject.vue';
import UpdatePs from '@/components/intervenants/UpdatePs.vue';
import AddPs from '@/components/intervenants/AddPs.vue';
import ListPSView from '@/components/intervenants/ListPS.vue';
import  UserCalendar from '@/components/intervenants/UserCalendar.vue';
import  MyCalendarView from '@/components/intervenants/MyCalendar.vue';
import ClientsComponent from '@/components/clients/ClientsComponent.vue';
import AddClientComponent from '@/components/clients/AddClientComponent.vue';
import UpdateClientComponent from '@/components/clients/UpdateClientComponent.vue';
import Domain_competence from '@/components/intervenants/Domain_competence.vue';
import Niveau_competence from '@/components/intervenants/Niveau_competence.vue';
import ChangePassword from '@/components/Auth/ChangePassword.vue';
import JoursFeries from '@/components/JoursFeries.vue';
import ListAgence from '@/components/agence/ListAgence.vue';
import DemandeUser from '@/components/demandes/DemandeUser.vue';
import DemandeAdmin from '@/components/demandes/DemandeAdmin.vue';


const routes = [

  {
    path: '/forgotComponent',
    name: 'ForgotComponent',
    component: ForgotComponentView
  },
  {
    path: '/reset-password/:token',
    name: 'ResetPassword',
    component: ResetPassword
  },
  {
    path: '/change-password',
    name: 'ChangePassword',
    component: ChangePassword
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/project',
    name: 'project',
    component: ProjectComponnent
  },
  {
    path: '/addps',
    name: 'addps',
    component: AddPs
  },
  {
    path: '/updateps/:id',
    name: 'updateps',
    component: UpdatePs,
    props: true 
  },
  {
    path: '/addclient',
    name: 'addclient',
    component: AddClientComponent
  },
  {
    path: '/domain',
    name: 'domain',
    component: Domain_competence
  },
  {
    path: '/niveau',
    name: 'niveau',
    component: Niveau_competence
  },
  {
    path: '/updateclient/:id',
    name: 'updateclient',
    component: UpdateClientComponent,
    props: true 
  },
  {
    path: '/usercalendar/:userId',
    name: 'userCalendar', // Nom de la route
    component: UserCalendar,
    props: true, // Permet de passer les paramètres de route comme props
  },
  {
    path: '/updateproject',
    name: 'updateproject',
    component: updateproject
  },
  
  {
    path: '/myprofile',
    name: 'MyProfile',
    component: () => import('@/components/intervenants/MyProfile.vue'), // Lazy load import MyProfile from '@/components/intervenants/MyProfile.vue';

  },
 
  {
    path: '/',
    component: NavView,
    children: [
      {
        path: '',
        name: 'Home',
        components: { one: HomeView },
      },
      {
        path: 'projet',
        name: 'projet',
        components: { one: ProjetView },
      },
      {
        path: 'about',
        name: 'About',
        components: { one: AboutView },
      },
      {
        path: 'listPs',
        name: 'listPs',
        components: { one: ListPSView },
      },
      {
        path: 'clients',
        name: 'clients',
        components: { one: ClientsComponent },
      },
      {
        path: 'ferie',
        name: 'JoursFeries',
        components: { one: JoursFeries },
      },
      {
        path: 'mycalendar',
        name: 'mycalendar',
        components: { one: MyCalendarView },
      },
      {
        path: 'listAgence',
        name: 'ListAgence',
        components: { one: ListAgence },
      },
      {
        path: 'demande',
        name: 'DemandeUser',
        components: { one: DemandeUser },
      },
      {
        path: 'demandes',
        name: 'DemandeAdmin',
        components: { one: DemandeAdmin },
      },
    ],
  }

]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
