<template>
  <div class="flex items-center justify-center min-h-screen bg-gray-100">
    <div class="px-8 py-6 mt-4 text-left bg-white shadow-lg">
      <h3 class="text-2xl font-bold text-center">Changement de mot de passe</h3>
      <form @submit.prevent="changePassword">
        <div class="mt-4">
          <div>
            <label class="block" for="email">Email</label>
            <input
              v-model="email"
              type="email"
              placeholder="Votre email"
              id="email"
              class="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-600"
            />
          </div>
          <div class="mt-4">
            <label class="block" for="currentPassword">Mot de passe actuel</label>
            <input
              v-model="currentPassword"
              type="password"
              placeholder="Mot de passe actuel"
              id="currentPassword"
              class="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-600"
            />
          </div>
          <div class="mt-4">
            <label class="block" for="newPassword">Nouveau mot de passe</label>
            <input
              v-model="newPassword"
              type="password"
              placeholder="Nouveau mot de passe"
              id="newPassword"
              class="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-600"
            />
          </div>
          <div class="mt-4">
            <label class="block" for="confirmPassword"
              >Confirmer le nouveau mot de passe</label
            >
            <input
              v-model="confirmPassword"
              type="password"
              placeholder="Confirmer le nouveau mot de passe"
              id="confirmPassword"
              class="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-1 focus:ring-blue-600"
            />
          </div>
          <div class="flex items-baseline justify-between">
            <button
              class="px-6 py-2 mt-4 text-white bg-blue-600 rounded-lg hover:bg-blue-900"
            >
              Changer le mot de passe
            </button>
          </div>
        </div>
      </form>
      <div v-if="errorMessage" class="mt-4 text-center text-red-500">
        {{ errorMessage }}
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  props: {
    temporaryToken: {
      type: String,
      default: null,
    },
  },
  name: "ChangePassword",
  data() {
    return {
      email: "", // Ajout de l'email
      currentPassword: "",
      newPassword: "",
      confirmPassword: "",
      errorMessage: "",
    };
  },
  methods: {
    async changePassword() {
      if (this.newPassword !== this.confirmPassword) {
        this.errorMessage = "Les nouveaux mots de passe ne correspondent pas.";
        return;
      }

      try {
        let token = this.temporaryToken || localStorage.getItem("token");
        const response = await axios.post(
          "http://127.0.0.1:8000/api/change-password",
          {
            email: this.email,
            old_password: this.currentPassword,
            password: this.newPassword,
            password_confirmation: this.confirmPassword,
          },
          {
            headers: {
              Authorization: `Bearer ${token}`,
              Accept: "application/json",
            },
          }
        );

        // Vérifiez si la réponse contient le message de succès
        if (response.data.message === "Votre mot de passe a été changé avec succès.") {
          // Redirige vers l'accueil
          this.$router.push("/login");
          return; // Sortir de la méthode après la redirection
        }

        // Vous pouvez également gérer d'autres messages ou le succès ici si nécessaire
        if (response.data.success) {
          if (response.data.token) {
            localStorage.setItem("token", response.data.token);
          }

          let user = JSON.parse(localStorage.getItem("user")) || {};
          user.firstConnect = "NO";
          localStorage.setItem("user", JSON.stringify(user));

          this.$router.go(user.type === 0 ? "/mycalendar" : "/projet");
        }
      } catch (error) {
        console.error("Erreur complète:", error);
        if (error.response) {
          console.error("Réponse du serveur:", error.response.data);
          if (error.response.data.errors) {
            this.errorMessage = error.response.data.errors.join(", ");
          } else if (error.response.data.message) {
            this.errorMessage = error.response.data.message;
          } else {
            this.errorMessage = "Une erreur s'est produite. Veuillez réessayer.";
          }
        } else {
          this.errorMessage = "Aucune réponse du serveur. Veuillez réessayer plus tard.";
        }
      }
    },
  },
};
</script>
