<template>
  <div class="flex items-center justify-center h-screen overflow-hidden bg-gradient-to-b from-gray-50 to-gray-100">
    <div class="w-1/3 text-center space-y-7">
      <!-- Titre de la page -->
      <div class="mb-4">
        <h1 class="text-4xl font-extrabold tracking-wide text-transparent uppercase bg-clip-text bg-gradient-to-r from-pink-500 to-violet-500">
          Bienvenue sur SBS Manager
        </h1>
      </div>

      <!-- Description -->
      <div>
        <h2 class="text-lg text-gray-600">
          Votre plateforme de gestion du Staffing des Collaborateurs
        </h2>
      </div>

      <!-- Bouton Commencer avec effets iOS -->
      <div>
        <button
          @click="handleStart"
          class="px-8 py-3 text-lg font-semibold text-white transition duration-300 ease-in-out transform bg-blue-600 rounded-full shadow-lg hover:bg-blue-700 hover:scale-105 focus:ring-4 focus:ring-blue-300 focus:outline-none"
        >
          Commencer
        </button>
      </div>
    </div>

    <!-- Image de fond avec effet iOS et espace autour -->
    <div class="w-2/3 p-6">
      <img
        alt="Vue img"
        src="../assets/ban.jpeg"
        class="object-cover object-center w-full h-full transition-transform duration-300 rounded-lg shadow-lg hover:scale-102"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "HomeView",

  methods: {
    handleStart() {
      const token = localStorage.getItem("token"); // Vérifie si l'utilisateur est connecté
      const user = JSON.parse(localStorage.getItem("user")); // Récupère le type de l'utilisateur

      if (token && user) {
        // Redirection basée sur le type d'utilisateur
        if (user.type === 0) {
          this.$router.push("/mycalendar"); // Redirige vers MyCalendar si type 0
        } else {
          this.$router.push("/projet"); // Redirige vers Projet pour les autres types
        }
      } else {
        this.$router.push("/login"); // Redirige vers Login si non connecté
      }
    },
  },
};
</script>

