      </nav>

      <!-- Mobile Menu -->
      <div v-if="mobileMenuOpen" class="fixed inset-0 z-40 bg-white shadow-lg">
        <div class="flex flex-col p-4 space-y-4">
          <router-link
            to="/"
            class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
            >Accueil</router-link
          >

          <!-- Lien visible pour les utilisateurs avec userType 1 ou 2 -->
          <router-link
            v-if="userType === 4 || userType === 5"
            to="/projet"
            class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
            >Staffing</router-link
          >
          
          <router-link
            v-if="userType === 4 || userType === 5"
            to="/listPs"
            class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
            >Mes consultants</router-link
          >
           <router-link
  v-if="userType === 4 || userType === 5"
  to="/listAgence"
  class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
  >Mes Agences</router-link
>
          <router-link
            v-if="userType === 4 || userType === 5"
            to="/clients"
            class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
            >Mes Clients</router-link
          >
          <button
            @click="toggleCompetences"
            v-if="userType === 4 || userType === 5"
            class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
          >
            Compétences
          </button>
          <router-link
            v-if="userType === 4 || userType === 5"
            to="/ferie"
            class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
            >Férié</router-link
          >

          <!-- Lien visible uniquement pour userType 0 -->
          <router-link
            v-if="userType === 0 || userType === 1 || userType === 2 || userType === 3"
            to="/mycalendar"
            class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
            >MyCalendar</router-link
          >
           <router-link
           v-if="auth"
            to="/myprofile"
            class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
            >Mon Profil</router-link
          >

          <!-- Afficher Connexion si l'utilisateur n'est pas authentifié -->
          <router-link
            v-if="!auth"
            to="/login"
            class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
            >Connexion</router-link
          >

          <!-- Afficher Déconnexion si l'utilisateur est authentifié -->
          <router-link
            v-if="auth"
            to="/"
            @click="logout"
            class="p-2 text-base font-medium transition duration-200 hover:text-blue-900"
            >Déconnexion</router-link
          >
        </div>
      </div>

      <!-- Modal Compétences -->
      <div v-if="over" class="absolute z-50 right-[170px] mt-20 ">
        <div class="h-32 p-1 space-y-4 bg-white shadow-lg w-36 rounded-b-3xl">
          <div>
            <router-link to="/domain"
              ><button
                class="w-24 p-2 font-bold text-black capitalize bg-blue-100 rounded hover:bg-slate-300"
              >
                Domaines
              </button></router-link
            >
          </div>
          <div>
            <router-link to="/niveau"
              ><button
                class="w-24 p-2 font-bold text-black capitalize bg-blue-100 rounded hover:bg-slate-300"
              >
                Niveau
              </button></router-link
            >
          </div>
        </div>
      </div>
    </div>

    <!-- Vue spécifique selon la route -->
    <div class="h-20"></div>
    <router-view name="one"></router-view>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "NavView",
  data() {
    return {
      userType: null, // Par défaut, aucune information sur le type utilisateur
      auth: false, // Par défaut, l'utilisateur n'est pas authentifié
      over: false,
      mobileMenuOpen: false,
    };
  },
  mounted() {
    this.getUserInfo(); // Récupérer les informations de l'utilisateur lors du montage
  },
  methods: {
    async getUserInfo() {
      try {
        const response = await axios.get("http://127.0.0.1:8000/api/me", {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
        });
        this.userType = response.data.type; // Assurez-vous que `type` est bien dans la réponse
        this.auth = true;
      } catch (error) {
        console.error(
          "Erreur lors de la récupération des informations utilisateur",
          error
        );
        this.auth = false;
      }
    },
    logout() {
      // Supprimer le token lors de la déconnexion
      localStorage.removeItem("token");
      this.auth = false;
      this.userType = null;
      this.$router.push("/login"); // Rediriger vers la page de connexion
    },
    toggleCompetences() {
      this.over = !this.over; // Inverse l'état d'affichage du modal
    },
    toggleMobileMenu() {
      this.mobileMenuOpen = !this.mobileMenuOpen; // Inverse l'état d'affichage du menu mobile
    },
  },
  watch: {
    $route() {
      if (this.over) {
        this.over = false; // Ferme le modal si une route change
      }
      if (this.mobileMenuOpen) {
        this.mobileMenuOpen = false; // Ferme le menu mobile si une route change
      }
    },
  },
};
</script>

<style scoped>
/* Styles pour un look professionnel avec effets iOS */
nav {
  background-color: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
nav:hover {
  background-color: #f9f9f9;
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
}
button {
  transition: transform 0.2s ease-in-out;
}
button:hover {
  transform: scale(1.05);
}
</style>
